import toggleLikeIcon from "../assets/image/toggleLikeIcon.png";
import commentIcon from "../assets/image/commentIcon.png";
import busIcon from "../assets/image/busIcon.png";
import personIcon from "../assets/image/peopleIcon.png";
import scheduleIcon from "../assets/image/orangeScheduleIcon.png";
import enrolledScIcon from "../assets/image/scCommunityIcon.png";
import blueRightIcon from "../assets/image/blueRightArrow.png";
const DetailFeed = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [isLoading, setIsLoading] = useState(false); //true로 바꿀것
  const [feedData, setFeedData] = useState([]);
  const FeedId = searchParams.get("feedID");
  console.log(FeedId);

  const getData = async () => {
    setIsLoading(true);

    await axios({
      method: "get",
      url: `${process.env.REACT_APP_API_URL}/feed/feed1?feedId=${FeedId}`,
    })
      .then((res) => {
        console.log(res.data);
        if (res.data.isPass) {
          setFeedData(res.data.feedDetail);
          setIsLoading(false);
        }
      })
      .catch((err) => {
        console.log(err);
        alert("일시적으로 정보를 불러올 수 없습니다.");
        setIsLoading(false);
      });
  };
  useEffect(() => {
    getData();
  }, []);

  function formatDate(dateString) {
    const dateObject = new Date(dateString);

    const year = dateObject.getFullYear();
    const month = String(dateObject.getMonth() + 1).padStart(2, "0");
    const day = String(dateObject.getDate()).padStart(2, "0");

    return `${year}-${month}-${day} `;
  }

  return (
    <>
      {isLoading ? (
        <LoadingBox>
          <ClipLoader color="#25B1FA" />
        </LoadingBox>
      ) : (
        <Container>
          <CommunityContainer>
            <CommunityTopArea>
              <CommunityUserInfoArea>
                <CommunityUserInfoImg src={feedData?.user_profile} />
                <CommunityUserName>{feedData?.user_name}</CommunityUserName>
              </CommunityUserInfoArea>
            </CommunityTopArea>
            <CommunityPostImageArea>
              {/* <CommunityPostImg src={feedData?.imgs[0]} /> */}
              {feedData && feedData.imgs && feedData.imgs.length > 0 && (
                <CommunityPostImg src={feedData.imgs[0]} />
              )}
              {/* {feedData?.oneDayTitle ? (
                <>
                  <CommunityDayTripArea>
                    <CommunityDayTripSpeakerImg src={busIcon} />
                    <CommunityDayTripTitleText>{feedData?.oneDayTitle}</CommunityDayTripTitleText>
                  </CommunityDayTripArea>
                  <div>
                    <CommunityInfoPersonView>
                      <CommunityInfoIcon src={personIcon} />
                      {feedData?.recruit === "단체" ? (
                        <CommunityBlueText>
                          현재 모집인원: {feedData?.recruitNumber}/{feedData?.maxRecruit}
                        </CommunityBlueText>
                      ) : (
                        <CommunityBlueText>
                          현재 모집인원: {feedData?.recruitNumber}
                        </CommunityBlueText>
                      )}
                    </CommunityInfoPersonView>
                    <CommunityInfoDateView>
                      <CommunityInfoIcon src={scheduleIcon} />
                      <CommunityBlueText>
                        여행일: {feedData?.oneDayStartDate}~{feedData?.oneDayEndDate}{" "}
                      </CommunityBlueText>
                    </CommunityInfoDateView>
                  </div>
                </>
              ) : null} */}
            </CommunityPostImageArea>
            <CommunityIconArea>
              <CommunityLeftIconArea>
                {feedData.isLiked ? (
                  <CommunityIconImg src={toggleLikeIcon} />
                ) : (
                  <CommunityIconImg src={emptyLikeIcon} />
                )}
                <CommunityIconText>{feedData.likeCount}</CommunityIconText>
                <CommunityIconImg style={{ marginLeft: 16 }} src={commentIcon} />
                <CommunityIconText>{feedData.commentCount}</CommunityIconText>
              </CommunityLeftIconArea>
            </CommunityIconArea>
            <CommunityPostTitleArea>
              <CommunityPostTitleText>{feedData?.text}</CommunityPostTitleText>
            </CommunityPostTitleArea>
            <CommunityBottomView>
              <CommunityPostDateText>{formatDate(feedData?.createdAt)}</CommunityPostDateText>
            </CommunityBottomView>
          </CommunityContainer>
        </Container>
      )}
    </>
  );
};
